import { stringify } from '@angular/compiler/src/util';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable()
export class LotterService {
  lotPersons: LOTTPERSON[] = [];

  sendPerson$: Subject<any> = new Subject();
  clearAll$: Subject<any> = new Subject();

  constructor() {}

  addPersonData(p: LOTTPERSON) {
    this.lotPersons.push(p);
    this.sendPerson$.next(this.lotPersons);
  }

  clearPersonData() {
    this.lotPersons = [];
    localStorage.removeItem('lotttPersons');
    this.sendPerson$.next([]);
    this.clearAll$.next(true);
  }

  public storePersonData(data) {
    let saveData = JSON.stringify(data);
    localStorage.setItem('lotttPersons', saveData);
  }

  public checkPerson() {
    let getData = localStorage.getItem('lotttPersons') || '';
    let data = getData ? JSON.parse(getData) : [];
    if (data.length > 0) {
      setTimeout(() => {  this.sendPerson$.next(data);},200)
    }
  }

  get allPersonData() {
    return this.lotPersons;
  }
}

export interface LOTTPERSON {
  name: string;
  lastNum: string;
}
